/*=============================
	1. Google Fonts
===============================*/
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');

/*=============================
	01. Variable CSS
===============================*/
:root {
    --tg-body-font-family        : 'Poppins', sans-serif;
    --tg-heading-font-family     : 'Poppins', sans-serif;
    --tg-primary-color           : #3482FF;
    --tg-secondary-color         : #F89E52;
    --tg-body-font-color         : #272727;
    --tg-heading-font-color      : #0B0B0B;
    --tg-banner-title-color      : #2A2A2A;
    --tg-paragraph-color         : #272727;
    --tg-body-font-size          : 16px;
    --tg-body-font-weight        : 400;
    --tg-heading-font-weight     : 700;
    --tg-body-line-height        : 1.7;
    --tg-heading-line-height     : 1.2;
    --tg-section-background      : #292F3F;
    --tg-section-background-two  : #1F1D28;
    --tg-section-background-three: #26242D;
    --tg-menu-color              : #252541;
    --tg-arrow-color             : #6F7C90;
    --tg-border-color            : #F3F3F3;
    --tg-border-color-two        : #D9D9D9;
    --tg-form-border-color       : #D5D5D5;
    --tg-old-lace                : #FFF2E4;
    --tg-magnolia                : #F0EBFF;
    --tg-lavender                : #E4F1FF;
    --tg-blue                    : #2B78F3;
    --tg-blue-two                : #265FDB;
    --tg-blue-two-three          : #398DDB;
    --tg-indigo                  : #A666F7;
    --tg-purple                  : #9270FF;
    --tg-dark-electric           : #605E7A;
    --tg-pink                    : #F84D55;
    --tg-red                     : #F76766;
    --tg-orange                  : #F26F3B;
    --tg-yellow                  : #F1F026;
    --tg-yellow-two              : #F5A623;
    --tg-yellow-three            : #ECB321;
    --tg-green                   : #1B9A81;
    --tg-green-two               : #7DDB39;
    --tg-teal                    : #57C0CE;
    --tg-cyan                    : #0dcaf0;
    --tg-white                   : #ffffff;
    --tg-black                   : #292930;
    --tg-black-two               : #1D1D25;
    --tg-black-three             : #1E1C1E;
    --tg-black-four              : #212737;
    --tg-gray                    : #F4F3F9;
    --tg-gray-two                : #FBFDFF;
    --tg-gray-three              : #7E7E8C;
    --tg-gray-four               : #B8B8C5;
    --tg-gray-five               : #818080;
    --tg-gray-six                : #F7F7F7;
    --tg-gray-seven              : #6C7183;
    --tg-gray-eight              : #F4F4F4;
    --tg-granite-gray            : #2A2A2A;
    --tg-bright-gray             : #EDEEF1;
    --tg-spanish-gray            : #989898;
    --tg-cadet-grey              : #92A5B4;
    --tg-cultured                : #F3F7FA;
    --tg-black-coral             : #5F5F71;
    --tg-silver                  : #C5C7CB;
    --tg-silver-two              : #AEAEAE;
    --tg-silver-three            : #AFAFAF;
    --tg-silver-three            : #8D959B;
    --tg-silver-foil             : #B0B0B0;
    --tg-manatee                 : #9E9DA9;
    --tg-onyx                    : #1D1D25;
    --facebook                   : #3b5998;
    --twitter                    : #00acee;
    --linkedin                   : #1976d2;
    --pinterest                  : #3b5998;
    --youtube                    : #c4302b;
    --skype                      : #00aff0;
}

body {
    font-family: var(--tg-body-font-family);
    font-weight: var(--tg-body-font-weight);
    font-size  : var(--tg-body-font-size);
    line-height: var(--tg-body-line-height);
    color      : var(--tg-body-font-color);
    font-style : normal;
}

.black-background {
    background: var(--tg-black);
}

img,
.img {
    max-width : 100%;
    transition: all 0.3s ease-out 0s;
}

.f-left {
    float: left
}

.f-right {
    float: right
}

.fix {
    overflow: hidden
}

a,
button {
    -webkit-transition: all 0.3s ease-out 0s;
    -moz-transition   : all 0.3s ease-out 0s;
    -ms-transition    : all 0.3s ease-out 0s;
    -o-transition     : all 0.3s ease-out 0s;
    transition        : all 0.3s ease-out 0s;
    text-decoration   : none;
}

a:focus,
.btn:focus,
button:focus {
    text-decoration: none;
    outline        : none;
    box-shadow     : none;
}

a:hover,
.portfolio-cat a:hover,
.footer -menu li a:hover {
    color          : var(--tg-primary-color);
    text-decoration: none;
}

a,
button {
    color          : var(--tg-primary-color);
    outline        : medium none;
    text-decoration: none;
}

.btn:focus,
button:focus,
input:focus,
input:focus,
textarea,
textarea:focus {
    outline: 0
}

.uppercase {
    text-transform: uppercase;
}

.capitalize {
    text-transform: capitalize;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family   : var(--tg-heading-font-family);
    color         : var(--tg-heading-font-color);
    margin-top    : 0px;
    margin-bottom : .7rem;
    font-style    : normal;
    line-height   : var(--tg-heading-line-height);
    font-weight   : var(--tg-heading-font-weight);
    text-transform: inherit;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
    color: inherit;
}

h1 {
    font-size: 2.5rem;
}

h2 {
    font-size: 2rem;
}

h3 {
    font-size: 1.75rem;
}

h4 {
    font-size: 1.5rem;
}

h5 {
    font-size: 1.25rem;
}

h6 {
    font-size: 1rem;
}

.list-wrap {
    margin : 0px;
    padding: 0px;
}

.list-wrap li {
    list-style: none
}

p {
    font-size    : var(--tg-body-font-size);
    font-weight  : var(--tg-body-font-weight);
    line-height  : var(--tg-body-line-height);
    color        : var(--tg-paragraph-color);
    margin-bottom: 15px;
}

hr {
    border-bottom: 1px solid var(--tg-primary-color);
    border-top   : 0 none;
    margin       : 30px 0;
    padding      : 0;
}

label {
    color      : var(--tg-body-font-color);
    cursor     : pointer;
    font-size  : 16px;
    font-weight: 400;
}

*::-moz-selection {
    background : var(--tg-primary-color);
    color      : var(--tg-white);
    text-shadow: none;
}

::-moz-selection {
    background : var(--tg-primary-color);
    color      : var(--tg-white);
    text-shadow: none;
}

::selection {
    background : var(--tg-primary-color);
    color      : var(--tg-white);
    text-shadow: none;
}

*::-moz-placeholder {
    color    : var(--tg-body-font-color);
    font-size: var(--tg-body-font-size);
    opacity  : 1;
}

*::placeholder {
    color    : var(--tg-body-font-color);
    font-size: var(--tg-body-font-size);
    opacity  : 1;
}

.theme-overlay {
    position: relative
}

.theme-overlay::before {
    background: var(--tg-primary-color) none repeat scroll 0 0;
    content   : "";
    height    : 100%;
    left      : 0;
    opacity   : 0.6;
    position  : absolute;
    top       : 0;
    width     : 100%;
}

.separator {
    border-top: 1px solid var(--tg-primary-color);
}

/* Bootstrap 5 */
.container {
    padding-left : 15px;
    padding-right: 15px;
}

.row {
    --bs-gutter-x: 30px;
}

.row.g-0 {
    --bs-gutter-x: 0;
}

.gutter-y-30 {
    --bs-gutter-y: 30px;
}

/*=============================
	1. Button style
===============================*/
.btn {
    user-select   : none;
    background    : var(--tg-primary-color) none repeat scroll 0 0;
    border        : medium none;
    border-radius : 20px;
    color         : var(--tg-white);
    cursor        : pointer;
    display       : inline-block;
    font-size     : 16px;
    font-weight   : 600;
    letter-spacing: 0;
    line-height   : 1;
    margin-bottom : 0px;
    padding       : 28px 55px;
    text-align    : center;
    text-transform: capitalize;
    touch-action  : manipulation;
    transition    : all 700ms ease 0s;
    vertical-align: middle;
    white-space   : nowrap;
    position      : relative;
    overflow      : hidden;
    z-index       : 1;
}

.btn span {
    background        : black none repeat scroll 0 0;
    border-radius     : 50%;
    display           : block;
    height            : 0;
    position          : absolute;
    transform         : translate(-50%, -50%);
    -moz-transform    : translate(-50%, -50%);
    -webkit-transform : translate(-50%, -50%);
    -ms-transform     : translate(-50%, -50%);
    -o-transform      : translate(-50%, -50%);
    transition        : width 0.6s ease 0s, height 0.6s ease 0s;
    -moz-transition   : width 0.6s ease 0s, height 0.6s ease 0s;
    -webkit-transition: width 0.6s ease 0s, height 0.6s ease 0s;
    -ms-transition    : width 0.6s ease 0s, height 0.6s ease 0s;
    -o-transition     : width 0.6s ease 0s, height 0.6s ease 0s;
    width             : 0;
    z-index           : -1;
}

.btn:hover>span {
    height: 562.5px;
    width : 562.5px;
}

.btn:hover {
    color: var(--tg-white);
}

.btnblack {
    user-select   : none;
    background    : var(--tg-black) none repeat scroll 0 0;
    border        : medium none;
    border-radius : 20px;
    color         : var(--tg-white);
    cursor        : pointer;
    display       : inline-block;
    font-size     : 16px;
    font-weight   : 600;
    letter-spacing: 0;
    line-height   : 1;
    margin-bottom : 0px;
    padding       : 28px 55px;
    text-align    : center;
    text-transform: capitalize;
    touch-action  : manipulation;
    transition    : all 700ms ease 0s;
    vertical-align: middle;
    white-space   : nowrap;
    position      : relative;
    overflow      : hidden;
    z-index       : 1;
}


.btnblack span {
    background        : var(--tg-primary-color) none repeat scroll 0 0;
    border-radius     : 50%;
    display           : block;
    height            : 0;
    position          : absolute;
    transform         : translate(-50%, -50%);
    -moz-transform    : translate(-50%, -50%);
    -webkit-transform : translate(-50%, -50%);
    -ms-transform     : translate(-50%, -50%);
    -o-transform      : translate(-50%, -50%);
    transition        : width 0.6s ease 0s, height 0.6s ease 0s;
    -moz-transition   : width 0.6s ease 0s, height 0.6s ease 0s;
    -webkit-transition: width 0.6s ease 0s, height 0.6s ease 0s;
    -ms-transition    : width 0.6s ease 0s, height 0.6s ease 0s;
    -o-transition     : width 0.6s ease 0s, height 0.6s ease 0s;
    width             : 0;
    z-index           : -1;
}

.btnblack:hover>span {
    height: 562.5px;
    width : 562.5px;
}

.btnblack:hover {
    color: var(--tg-white);
}

.breadcrumb>.active {
    color: var(--tg-primary-color);
}

/* scrollUp */
.scroll-top {
    width        : 40px;
    height       : 40px;
    line-height  : 40px;
    position     : fixed;
    bottom       : -10%;
    right        : 30px;
    font-size    : var(--tg-body-font-size);
    border-radius: 6px;
    z-index      : 99;
    color        : var(--tg-white);
    text-align   : center;
    cursor       : pointer;
    background   : var(--tg-primary-color);
    transition   : 1s ease;
    border       : none;
}

.scroll-top.open {
    bottom: 30px;
}

.scroll-top::after {
    position  : absolute;
    z-index   : -1;
    content   : '';
    top       : 100%;
    left      : 5%;
    height    : 10px;
    width     : 90%;
    opacity   : 1;
    background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0.25) 0%, rgba(0, 0, 0, 0) 80%);
}

.scroll-top:hover {
    background: black;
}


/*=============================
	02. Header
===============================*/
.custom-container {
    max-width: 1400px;
}
.servieces-container {
    max-width: 1100px;
}
.transparent-header {
    position: absolute;
    left    : 0;
    top     : 0px;
    width   : 100%;
    z-index : 9;
    height  : auto;
}

.menu-area {
    padding: 7px 100px;
}

.menu-nav {
    display        : flex;
    align-items    : center;
    flex-wrap      : wrap;
    justify-content: flex-start;
}

.navbar-wrap {
    display  : flex;
    flex-grow: 1;
}

.navbar-wrap ul {
    display       : flex;
    flex-direction: row;
    flex-wrap     : wrap;
    padding       : 0 0;
    margin        : 0 0 0 auto;
}

.navbar-wrap ul li {
    list-style: none;
    display   : block;
    position  : relative;
}

.navbar-wrap ul li a {
    font-size     : 16px;
    font-weight   : 500;
    text-transform: capitalize;
    color         : var(--tg-menu-color);
    padding       : 35px 18px;
    display       : block;
    line-height   : 1;
    position      : relative;
    z-index       : 1;
}

.navbar-wrap>ul>li.active>a,
.navbar-wrap>ul>li:hover>a {
    color: var(--tg-primary-color);
}

.navbar-wrap>ul>li.menu-item-has-children>a::after {
    content    : "\f078";
    font-family: "Font Awesome 5 Pro";
    font-weight: 400;
    margin-left: 10px;
}

.main-menu .navigation li.menu-item-has-children .dropdown-btn {
    display: none;
}

.header-action>ul {
    display    : flex;
    align-items: center;
    margin-left: 55px;
}

.header-action>ul li {
    position   : relative;
    margin-left: 30px;
}

.header-action ul li:first-child {
    margin-left: 0;
}

.header-action ul li a {
    color    : var(--tg-heading-font-color);
    font-size: 14px;
}

.header-action .header-btn .btn {
    color      : var(--tg-white);
    font-size  : 16px;
    padding    : 19px 33px;
    font-weight: 500;
    background : var(--tg-primary-color);
}

.header-action .header-btn .btn span {
    background: black;
}

.navbar-wrap ul li .sub-menu {
    position          : absolute;
    left              : 0;
    right             : 0;
    top               : 100%;
    min-width         : 230px;
    border            : 1px solid #f5f5f5;
    background        : var(--tg-white);
    margin            : 0 0;
    transform         : scale(1, 0);
    transform-origin  : 0 0;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition   : all 0.3s ease-in-out;
    -ms-transition    : all 0.3s ease-in-out;
    -o-transition     : all 0.3s ease-in-out;
    transition        : all 0.3s ease-in-out;
    -webkit-box-shadow: 0px 30px 70px 0px rgba(137, 139, 142, 0.15);
    -moz-box-shadow   : 0px 30px 70px 0px rgba(137, 139, 142, 0.15);
    box-shadow        : 0px 30px 70px 0px rgba(137, 139, 142, 0.15);
    border-radius     : 15px;
    padding           : 18px 0;
    display           : block;
    visibility        : hidden;
    opacity           : 0;
    z-index           : 9;
}

.navbar-wrap ul li .sub-menu .sub-menu {
    right: auto;
    left : 100%;
    top  : 0;
}

.navbar-wrap ul li .sub-menu li {
    margin-left: 0;
    text-align : left;
    display    : block;
}

.navbar-wrap ul li .sub-menu li a {
    padding           : 9px 15px 9px 25px;
    line-height       : 1.4;
    font-weight       : 500;
    color             : var(--tg-menu-color);
    text-transform    : capitalize;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition   : all 0.3s ease-in-out;
    -ms-transition    : all 0.3s ease-in-out;
    -o-transition     : all 0.3s ease-in-out;
    transition        : all 0.3s ease-in-out;
}

.navbar-wrap ul li .sub-menu li a:hover,
.navbar-wrap ul li .sub-menu li.active a {
    color: var(--tg-primary-color);
}

.navbar-wrap ul li:hover>.sub-menu {
    opacity   : 1;
    visibility: visible;
    transform : scale(1);
}

.sticky-menu {
    position          : fixed;
    left              : 0;
    margin            : auto;
    top               : 0;
    width             : 100%;
    z-index           : 99;
    background        : var(--tg-white);
    -webkit-animation : 1000ms ease-in-out 0s normal none 1 running fadeInDown;
    animation         : 1000ms ease-in-out 0s normal none 1 running fadeInDown;
    -webkit-box-shadow: 0 10px 15px rgba(25, 25, 25, 0.1);
    box-shadow        : 0 10px 15px rgba(25, 25, 25, 0.1);
    border-radius     : 0;
}

.sticky-menu.menu-area {
    padding: 0 100px;
}

.sticky-menu .navbar-wrap ul li .sub-menu {
    border-radius: 0 0 15px 15px;
}

.menu-style-two .navbar-wrap ul li .sub-menu {
    background  : var(--tg-black-two);
    border-color: #2a2a34;
    box-shadow  : 0px 30px 70px 0px rgb(0 0 0 / 15%);
}

/* header-two */
.menu-style-two .navbar-wrap ul li a,
.menu-style-two .navbar-wrap ul li .sub-menu li a {
    color: var(--tg-white);
}

.menu-style-two .navbar-wrap>ul>li.active>a,
.menu-style-two .navbar-wrap>ul>li:hover>a {
    color: var(--tg-primary-color);
}

.menu-style-two.sticky-menu {
    background: var(--tg-black);
    box-shadow: 0 10px 15px rgb(0 0 0 / 10%);
}

/*=============================
	03. Mobile Menu
===============================*/
.nav-outer .mobile-nav-toggler {
    position    : relative;
    float       : right;
    font-size   : 40px;
    line-height : 50px;
    cursor      : pointer;
    display     : none;
    color       : var(--tg-white);
    margin-right: 30px;
    top         : 15px;
}

.nav-logo img {
    max-width: 150px;
}

.mobile-menu {
    position          : fixed;
    right             : 0;
    top               : 0;
    width             : 300px;
    padding-right     : 30px;
    max-width         : 100%;
    height            : 100%;
    z-index           : 99;
    border-radius     : 0px;
    transition        : all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
    -moz-transition   : all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
    -webkit-transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
    -ms-transition    : all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86)e;
    -o-transition     : all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
    -webkit-transform : translateX(101%);
    -ms-transform     : translateX(101%);
    transform         : translateX(101%);
}

.mobile-menu .navbar-collapse {
    display: block !important;
}

.mobile-menu .nav-logo {
    position  : relative;
    padding   : 30px 25px;
    text-align: left;
}

.mobile-menu-visible {
    overflow: hidden;
}

.mobile-menu-visible .mobile-menu {
    -webkit-transform: translateX(0%);
    -ms-transform    : translateX(0%);
    transform        : translateX(0%);
}

.mobile-menu .navigation li.current>a:before {
    height: 100%;
}

.menu-backdrop {
    position  : fixed;
    right     : 0px;
    top       : 0px;
    width     : 100%;
    height    : 100%;
    z-index   : 2;
    transition: all 700ms ease 0s;
    opacity   : 0;
    visibility: hidden;
    background: rgb(255 255 255 / 50%);
}

.mobile-menu-visible .menu-backdrop {
    opacity   : 1;
    visibility: visible;
}

.mobile-menu .menu-box {
    position  : absolute;
    left      : 0px;
    top       : 0px;
    width     : 100%;
    height    : 100%;
    max-height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    background: var(--tg-white);
    padding   : 0px 0px;
    z-index   : 5;
    box-shadow: -9px 0 14px 0px rgb(0 0 0 / 6%);
}

.mobile-menu-visible .mobile-menu .menu-box {
    opacity   : 1;
    visibility: visible;
}

.mobile-menu .close-btn {
    position          : absolute;
    right             : 15px;
    top               : 28px;
    line-height       : 30px;
    width             : 35px;
    text-align        : center;
    font-size         : 20px;
    color             : var(--tg-primary-color);
    cursor            : pointer;
    z-index           : 10;
    -webkit-transition: all 0.9s ease;
    -o-transition     : all 0.9s ease;
    transition        : all 0.9s ease;
}

.mobile-menu-visible .mobile-menu .close-btn {
    -webkit-transform: rotate(360deg);
    -ms-transform    : rotate(360deg);
    transform        : rotate(360deg);
}

.mobile-menu .navigation {
    position: relative;
    display : block;
    width   : 100%;
    float   : none;
    margin  : 0;
    padding : 0;
}

.mobile-menu .navigation ul {
    padding: 0;
    margin : 0;
}

.mobile-menu .navigation li {
    position  : relative;
    display   : block;
    border-top: 1px solid rgb(0 0 0 / 10%);
}

.mobile-menu .navigation:last-child {
    border-bottom: 1px solid rgb(0 0 0 / 10%);
}

.mobile-menu .navigation li>ul>li:first-child {
    border-top: 1px solid rgb(0 0 0 / 10%);
