    margin     : 0;
    padding    : 0;
    display    : flex;
    align-items: center;
    gap        : 10px;
    position   : absolute;
    left       : 16.4%;
    bottom     : -50px;
}

.slider-active .slick-dots li {
    list-style : none;
    line-height: 0;
}

.slider-active .slick-dots li button {
    text-indent  : -999999999px;
    width        : 16px;
    height       : 16px;
    border-radius: 50%;
    border       : 2px solid var(--tg-border-color-two);
    background   : transparent;
    position     : relative;
    transition   : .3s;
}

.slider-active .slick-dots li button::before {
    content      : "";
    position     : absolute;
    left         : 50%;
    top          : 50%;
    transform    : translate(-50%, -50%);
    width        : 7px;
    height       : 7px;
    border-radius: 50%;
    background   : var(--tg-primary-color);
    opacity      : 0;
    transition   : .3s;
}

.slider-active .slick-dots li.slick-active button {
    border-color: var(--tg-primary-color);
}

.slider-active .slick-dots li.slick-active button::before {
    opacity: 1;
}

.slider-shape-wrap img {
    position: absolute;
    z-index : -1;
}

.slider-shape-wrap img:nth-child(1) {
    left: 0;
    top : 0;
}

.slider-shape-wrap img:nth-child(2) {
    left     : 7%;
    top      : 28%;
    animation: animationFramesOne 25s infinite linear;
}

.slider-shape-wrap img:nth-child(3) {
    left     : 46%;
    bottom   : 37%;
    animation: ribbonRotate 8s infinite linear;
}

.slider-shape-wrap img:nth-child(4) {
    right    : 47%;
    top      : 32%;
    animation: contactSwimmer 10s infinite linear;
}

.slider-shape-wrap img:nth-child(5) {
    right: 0;
    top  : 0;
}

.slider-shape-wrap img:nth-child(6) {
    right    : 12%;
    top      : 22%;
    animation: alltuchtopdown 6s infinite linear;
}

.pulse::after {
    content      : "";
    position     : absolute;
    left         : 50%;
    top          : 50%;
    height       : 100%;
    width        : 100%;
    background   : var(--tg-primary-color);
    z-index      : -1;
    border-radius: 50%;
    transform    : translate(-50%, -50%);
    animation    : pulse-border 1500ms ease-out infinite;
}

@keyframes pulse-border {
    0% {
        transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
        opacity  : 1;
    }

    100% {
        transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
        opacity  : 0;
    }
}

@-webkit-keyframes pulse-border {
    0% {
        transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
        opacity  : 1;
    }

    100% {
        transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
        opacity  : 0;
    }
}

@keyframes alltuchtopdown {
    0% {
        transform: rotateX(0deg) translateY(0px);
    }

    50% {
        transform: rotateX(0deg) translateY(-40px);
    }

    100% {
        transform: rotateX(0deg) translateY(0px);
    }
}

