    flex : 0 0 auto;
    width: 69.8%;
}

.inner-blog-area .col-30 {
    flex : 0 0 auto;
    width: 30.2%;
}

.inner-blog-item {
    margin-bottom: 55px;
}

.inner-blog-item:last-child {
    margin-bottom: 0;
}

.inner-blog-thumb img {
    border-radius: 20px;
}

.inner-blog-thumb {
    margin-bottom: 50px;
}

.blog-meta-two .list-wrap {
    display      : flex;
    align-items  : center;
    gap          : 10px 0;
    margin-bottom: 20px;
    flex-wrap    : wrap;
}

.blog-meta-two .list-wrap li {
    position      : relative;
    margin-right  : 16px;
    padding-right : 16px;
    line-height   : 1.2;
    font-size     : 15px;
    letter-spacing: 0.122727px;
    color         : #717171;
}

.blog-meta-two .list-wrap li:last-child {
    margin-right : 0;
    padding-right: 0;
}

.blog-meta-two .list-wrap li::before {
    content   : "";
    position  : absolute;
    right     : 0;
    top       : 50%;
    transform : translateY(-50%);
    width     : 2px;
    height    : 20px;
    background: #F1F1F1;
}

.blog-meta-two .list-wrap li:last-child::before {
    display: none;
}

.blog-meta-two .list-wrap li i {
    color       : #F04463;
    margin-right: 6px;
}

.blog-meta-two .list-wrap li a {
    color: #F04463;
}

.blog-meta-two .list-wrap li.tag {
    padding-right: 0;
}

.blog-meta-two .list-wrap li.tag::before {
    display: none;
}

.blog-meta-two .list-wrap li.tag a {
    background   : var(--tg-secondary-color);
    color        : var(--tg-white);
    font-size    : 15px;
    border-radius: 7px;
    padding      : 3px 12px;
    display      : block;
}

.inner-blog-content .title {
    margin-bottom: 15px;
    font-weight  : 600;
    font-size    : 30px;
}

.inner-blog-content p {
    margin-bottom: 18px;
    width        : 94%;
}

.inner-blog-content .rade-more-btn {
    font-weight         : 600;
    font-size           : 16px;
    text-decoration-line: underline;
    color               : #5A5A5A;
}

.inner-blog-content .rade-more-btn:hover {
    color: var(--tg-primary-color);
}

.inner-blog-item:nth-child(2) .blog-meta-two .list-wrap li.tag a {
    background: #FC1AE5;
}

.inner-blog-item:nth-child(3) .blog-meta-two .list-wrap li.tag a {
    background: #FCBD1A;
}

.blog-sidebar {
    margin-left: 15px;
}

.blog-sidebar .widget {
    margin-bottom: 60px;
}

.blog-sidebar .widget:last-child {
    margin-bottom: 0;
}

.rc-post-item {
    display      : flex;
    align-items  : center;
    margin-bottom: 30px;
}

.rc-post-item:last-child {
    margin-bottom: 0;
}

.rc-post-item .thumb {
