}

.popular-post-item.big-post .content .title {
    margin-bottom: 10px;
    font-weight  : 600;
    font-size    : 16px;
}

.popular-post-item .content span {
    font-size: 14px;
    display  : block;
}

.popular-post-item .thumb {
    margin-bottom: 20px;
}

.popular-post-item .thumb img {
    border-radius: 10px;
    width        : 100%;
}

.popular-post-item .content .title {
    font-size    : 15px;
    font-weight  : 600;
    margin-bottom: 10px;
}

.popular-post-list .list-wrap {
    display  : flex;
    flex-wrap: wrap;
    margin   : 0 -10px;
}

.popular-post-list .list-wrap li {
    width  : 50%;
