}

.popular-post-list .list-wrap li:first-child {
    width: 100%;
}

.tag-list .list-wrap {
    display    : flex;
    align-items: center;
    flex-wrap  : wrap;
    gap        : 10px;
}

.tag-list .list-wrap li a {
    display      : block;
    font-size    : 14px;
