    right    : 21%;
    top      : 46%;
    animation: rotateme 10s infinite linear;
}

.newsletter-shape-wrap .shape-five {
    right    : 13%;
    top      : 27%;
    animation: animationFramesOne 35s infinite linear;
}

.newsletter-shape-wrap .shape-six {
    right    : 17%;
    bottom   : 24%;
