/*=============================
	31. Preloader
===============================*/
#preloader {
    background-color: var(--tg-white);
    height          : 100%;
    width           : 100%;
    position        : fixed;
    margin-top      : 0px;
    top             : 0px;
    z-index         : 9999;
}

#preloader .loader .loader-container {
    position     : absolute;
    top          : 50%;
    left         : 50%;
    transform    : translate(-50%, -50%);
    width        : 100px;
    height       : 100px;
    border       : 3px solid var(--tg-border-color);
    border-radius: 50%;
}

#preloader .loader .loader-container:before {
    position         : absolute;
    content          : "";
    display          : block;
    top              : 50%;
    left             : 50%;
    transform        : translate(-50%, -50%);
    width            : 100px;
    height           : 100px;
    border-top       : 3px solid var(--tg-primary-color);
    border-radius    : 50%;
    animation        : loaderspin 1.8s infinite ease-in-out;
    -webkit-animation: loaderspin 1.8s infinite ease-in-out;
}

#preloader .loader .loader-icon {
    position  : absolute;
    top       : 50%;
    left      : 50%;
    transform : translate(-50%, -50%);
    width     : 80px;
    text-align: center;
}

#preloader .loader .loader-icon img {
    animation: loaderpulse alternate 900ms infinite;
    width    : 40px;
}

@keyframes loaderspin {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
    }

    100% {
        transform: translate(-50%, -50%) rotate(360deg);
    }
}

@-webkit-keyframes loaderspin {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
    }

    100% {
        transform: translate(-50%, -50%) rotate(360deg);
    }
}

@keyframes loaderpulse {
    0% {
        transform: scale(1);
    }

    100% {
        transform: scale(1.2);
    }
}

/* Custom cursor */
.cursor-outer {
    margin-left: -18px;
    margin-top : -18px;
    width      : 35px;
    height     : 35px;
    background : var(--tg-primary-color);
    box-sizing : border-box;
    z-index    : 10000000;
    opacity    : .2;
    transition : all 0.3s ease-out;
}

.mouseCursor {
    position      : fixed;
    top           : 0;
    left          : 0;
    right         : 0;
    bottom        : 0;
    pointer-events: none;
    border-radius : 50%;
    transform     : translateZ(0);
    visibility    : hidden;
    text-align    : center;
}

[dir=rtl] .mouseCursor {
    left : 0;
    right: auto;
}

.cursor-inner {
    margin-left     : -4px;
    margin-top      : -4px;
    width           : 8px;
    height          : 8px;
    z-index         : 10000001;
    background-color: var(--tg-primary-color);
    transition      : width 0.3s ease-in-out, height 0.3s ease-in-out, margin 0.3s ease-in-out, opacity 0.3s ease-in-out;
}

.cursor-inner span {
    color         : var(--tg-white);
    line-height   : 80px;
    opacity       : 0;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size     : 12px;
}

.cursor-inner.cursor-big span {
    opacity: 1;
}

.mouseCursor.cursor-big {
    width              : 80px;
    height             : 80px;
    margin-inline-start: -40px;
    margin-top         : -40px;
}

.mouseCursor.cursor-big.cursor-outer {
    display: none;
}

.mouseCursor.cursor-big.d-none {
    display: none;
}

.slider-drag .swiper-slide {
    cursor: none;
}

.ripple-container {
    position: fixed;
    pointer-events: none;
    z-index: 9999; /* Ensure it is above other elements */
}

.ripple {
    position: absolute;
    width: 100px; /* Base width */
    height: 100px; /* Base height */
    border-radius: 50%;
    animation: flicker 1.5s infinite; /* Flicker animation */
    opacity: 0.8;
}

.fire {
    background: radial-gradient(circle, rgba(255, 69, 0, 1) 0%, rgba(255, 140, 0, 0.8) 60%, rgba(255, 255, 255, 0) 100%);
    box-shadow: 0 0 20px rgba(255, 69, 0, 0.8), 0 0 30px rgba(255, 140, 0, 0.5);
}

@keyframes flicker {
    0% {
        transform: scale(0.5);
        opacity: 0.9;
    }
    10% {
        transform: scale(0.7);
        opacity: 0.9;
    }
    20% {
        transform: scale(0.6);
        opacity: 0.9;
    }
    30% {
        transform: scale(0.7);
        opacity: 0.9;
    }
    40% {
        transform: scale(0.5);
        opacity: 1;
    }
    50% {
        transform: scale(0.6);
        opacity: 0.8;
    }
    60% {
        transform: scale(0.7);
        opacity: 0.6;
    }
    70% {
        transform: scale(0.6);
        opacity: 0.7;
    }
    80% {
        transform: scale(0.5);
        opacity: 0.7;
    }
    90% {
        transform: scale(0.7);
        opacity: 0.7;
    }
    100% {
        transform: scale(0.6);
        opacity: 0.9;
    }
}

